@import url(https://fonts.googleapis.com/css2?family=Orelega+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Taviraj&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Faustina&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Taviraj&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Faustina&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
/* 
Made margin and padding 0 so that child elements are easier to work with
Scrollbar is designed here
*/

html, body {
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
/* Added nice shadow effect to make navbar seem above the rest of the page*/
#nav-wrap {
    position: fixed;
    width: 100%;
    height: 60px;
    background-color: white;
    box-shadow:
        0 2.8px 2.2px rgba(0, 0, 0, 0.02),
        0 6.7px 5.3px rgba(0, 0, 0, 0.015),
        0 9px 9px rgba(0, 0, 0, 0.01),
        0 10px 10px rgba(0, 0, 0, 0.005);
    z-index: 2;
}

#nav-container {
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

#title-nav-bar {
    font-family: 'Orelega One', cursive;
    margin-left: 100px;
    font-weight: 100;
    color: #120321;
    text-shadow: 2px 2px #d1caca;

}

/* When the screen is less than 800px, adjust the nav title to center */
@media only screen and (max-width: 800px) {
    #nav-container {
        -webkit-justify-content: center;
                justify-content: center;
    }

    #title-nav-bar {
        margin: 0;
    }


}

.post-wrap {
    list-style-type: none;
    z-index: 0;
    cursor: pointer;

}

.post-box-wrap:hover {
    box-shadow:
        0 2.8px 2.2px rgba(0, 0, 0, 0.04),
        0 6.7px 5.3px rgba(0, 0, 0, 0.03),
        0 9px 9px rgba(0, 0, 0, 0.02),
        0 10px 10px rgba(0, 0, 0, 0.01);

    -webkit-transform: scale(1.01);

            transform: scale(1.01);
    border: 1px solid green;
}
.post-box-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: rgb(255, 255, 255);
    height: 80px;
    width: 90vw;
    max-width: 1000px;
    margin: 5px 0px 5px 0px;
    padding: 10px 5px 5px 40px;
    transition: all .2s ease-in-out;

    
    border: 1px solid rgb(233, 230, 230);
    border-radius: 3px;
    box-shadow:
        0 2.8px 2.2px rgba(0, 0, 0, 0.02),
        0 6.7px 5.3px rgba(0, 0, 0, 0.015),
        0 9px 9px rgba(0, 0, 0, 0.01),
        0 10px 10px rgba(0, 0, 0, 0.005);
}

.post-title {
    text-overflow: ellipsis; /* Very important to make overflow nicely */
    overflow: hidden;
    white-space: nowrap;
    font-family: 'Taviraj', serif;
    font-weight: 600;
    margin: 0;
    
}

.post-author {
    text-overflow: ellipsis; 
    overflow: hidden;
    white-space: nowrap;
    font-family: 'Faustina', serif;
    font-weight: 200;
    margin-top: 2px;
    color: gray
}

@media only screen and (max-width: 800px) {
    .post-box-wrap {
        padding-left: 20px;
    }

}
/* Allows for the shade in the background and modal to be centered */
#whole-wrap {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(189, 187, 187, 0.253);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 3;
}

#modal-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-order: 0;
            order: 0;
    max-height: 650px;
    height: 100%;
    max-width: 1100px;
    width: 100%;
    background-color: rgb(255, 255, 255);
    overflow-y: auto;
}

#close-btn {
    color:rgb(150, 148, 148);
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    margin-right: 15px;
    cursor: pointer;
}

#close-btn:hover {
    color: red;
}

#close-btn-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    width: 100%;
    padding: 15px 0px 0px 0px;
}

#post-container {
    padding: 20px 40px 20px 40px;
}

#post-title {
    font-family: 'Taviraj', serif;
    font-weight: 600;
    font-size: 30px;
}

#post-author {
    font-family: 'Faustina', serif;
    font-weight: 200;
    margin-top: 2px;
    color: gray;
    font-size: 20px;

}

#post-phrase {
    font-family: 'Faustina', serif;
    font-weight: 200;
    margin-top: -10px;
    color: rgb(172, 172, 172);
    font-size: 15px;
    margin-left: 20px;
}

#line-break-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}

#line-break {
    height: 1px;
    background-color: rgb(185, 185, 185);
    width: 90%
}

#post-body {
    margin-top: 20px;
    font-size: 30px;
    font-family: 'Roboto Condensed', sans-serif;
}

/*Hides the whole wrap*/
.hide {
    visibility: hidden;
}

.body-wrap {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #fcfafa;
}

#post-container-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;

  /* To make post list start below sticky navbar */
